export default{
	onLoad() {
	this.ip=uni.getStorageSync('ip')
	},
	data(){
		return{
			ip:''
		}
	},methods:{
		submit(){
			console.log('ip地址为:'+this.ip)
			uni.setStorageSync(
				'ip',
				this.ip
			)
			console.log('getip地址：'+uni.getStorageSync('ip'));
			uni.redirectTo({
			    url: 'login'
			});
		}
		
		 
	}	
}
